@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/7.0.0/normalize.min.css");
@import url("https://fonts.googleapis.com/css?family=Roboto");

$brown: #50514f !default;
$yellow: #ffe066 !default;
$green: #70c1b3 !default;
$turquoise: #247ba0 !default;
$blue: #247ba0 !default;
$red: #f25f5c !default;
$light: #ffffff !default;

html {
  font-size: 24px;
  font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    "Helvetica", "Arial", sans-serif;
}

body {
  background: #f4f4f4;
}

h1,
h2 {
  font-weight: 300;
}

h5 {
  font-weight: 300;
  span {
    background: lighten($brown, 10%);
    padding: 5px;
    border-radius: 4px;
    color: #fff;
    line-height: 1;
    font-size: 80%;
    display: inline-block;
    margin-left: 5px;
  }
}

h1 {
  font-size: 2rem;
}

section {
  padding: 1rem;
  background: #f9f9f9;

  h2 {
    text-align: center;
    span {
      font-size: 50%;
      display: block;
      max-width: 600px;
      margin: 0 auto;
      background: $light; // color:#fff;
      padding: 5px;
      border-radius: 4px;
    }
  }

  &:nth-child(odd) {
    background: #f0f0f0;
  }
  &.other-projects {
    background: $green;

    .projects {
      display: flex;
      justify-content: center;

      flex-wrap: wrap; // align-items: flex-start;
      .single-project {
        background: #f1f1f1;
        border-radius: 4px;
        width: 25%;
        padding: 10px;
        margin: 5px;
        img {
          max-width: 100%;
        }
        .project-title {
          text-align: center;
          color: #000;
          margin-top: 0;
          font-size: 1em;
          font-weight: 300;
        }

        &.gad {
          background: transparent;
        }
      }
    }
  }

  &.usage {
    background: $brown;
    color: #fff;
    padding-top: 2em;
    padding-bottom: 2em;

    a {
      color: $yellow;
    }

    code {
      background: darken($brown, 10%);
      padding: 0 5px;
      border-radius: 4px;
    }
    .html {
      background: $yellow;
      color: $brown;
      padding: 2px 4px;
      border-radius: 4px;
    }
  }

  &.hero {
    background: lighten($blue, 20%);
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 50px;

    h1 {
      margin: 0;
      float: left;
      span {
        font-size: 50%;
        background: darken($blue, 10%);
        padding: 2px 4px;
        border-radius: 4px;
      }
    }
    .links {
      float: right;
      svg {
        float: left;
      }
    }
  }
}

.flexbox {
  section {
    &.hero {
      h1,
      .links {
        float: none;
      }

      .links {
        display: flex;
        width: 500px;
        justify-content: flex-end;

        a {
          margin-left: 10px;
        }
      }
    }
  }
}

.content {
  display: block;

  .item {
    float: left;
    height: 200px;
    width: 280px;
    background: $brown;
    margin: 0.5rem;
    box-shadow: 0 0 5px #ccc;
    position: relative;

    .dark,
    .light {
      padding: 38px 115px;

      &.lg {
        padding: 35px 89px;
      }
    }
    .light {
      background: #fff;
    }

    &.light {
      background: $light;
    }

    &.special {
      &:after {
        content: "\2605";
        color: $yellow;
        position: absolute;
        font-size: 1.2em;
        left: 5px;
        top: 5px;
      }
    }

    &.text {
      .light {
        padding: 10px 20px;
        text-align: center;
      }
    }
  }
}

.flexbox .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

section {
  clear: both;
}

footer {
  font-weight: 100;
  text-align: center;
  a {
    color: darken($green, 30%);
    text-decoration: none;
    font-weight: inherit;
    text-align: center;
  }
}

.flexbox .item {
  display: flex;
  flex-direction: column;
  position: relative;

  .dark,
  .light {
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  .light {
  }

  div[class^="ckbx-"] {
  }

  &.light {
  }

  &.special {
    &:after {
    }
  }
}

.btn {
  background: $light;
  padding: 0.8em 1.6em;
  border-radius: 4px;
  font-size: 0.8rem;
  text-decoration: none;

  display: flex;
  align-items: center;
  color: $blue;

  svg {
    margin-right: 10px;
    fill: $blue;
  }

  &:hover {
    background: $brown;
    color: #fff;
    svg {
      fill: #fff;
    }
  }
}

.show-code,
.new {
  position: absolute;
  right: 5px;
  top: 5px;
  font-size: 0.5em;
  border: 0;
  outline: none;
  padding: 5px;
  background: $yellow;
  cursor: pointer;
  letter-spacing: 1px;
  font-weight: bolder;
  border-radius: 2px;
  color: $brown;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    background: darken($yellow, 20%);
  }
}

.new {
  right: auto;
  left: 5px;
  cursor: inherit;
  background: $light;
}

.code-modal:target {
  display: flex;
}

.code-modal {
  display: none;
  position: fixed;
  background: rgba(80, 81, 79, 0.9);
  top: 0;
  bottom: 0;
  left: 0; // display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  z-index: 9;

  .modal-content {
    background: #fff;
    flex-basis: 25vw;
    padding: 20px;
    border-radius: 10px;
    position: relative;
    font-size: 1rem;
  }
  .modal-close {
    text-decoration: none;
    position: absolute;
    right: 0;
    bottom: 102%; // background: #fff;
    color: #fff;
    padding: 5px;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 14px;
  }

  .flexbox {
    section {
      &.hero {
        flex-direction: column;

        h1 {
          margin-bottom: 10px;
          text-align: center;
          span {
            display: block;
          }
        }

        .links {
          flex-direction: column;
          width: auto;
          .btn {
            margin-left: 0;
            margin-bottom: 5px;
            justify-content: center;
          }
        }
      }

      .content {
        justify-content: center;
      }
    }
  }
}

.center {
  text-align: center;
}
